<template>
  <div>
    <mu-appbar style="width: 100%;" color="#4ed995" class="header">
      <a @click="backTo()">
        <mu-button icon slot="left" class="icon" size="20">
          <mu-icon value="keyboard_arrow_left"></mu-icon>
        </mu-button>
      </a>
      <span>数据填写</span>
    </mu-appbar>
    <div style="margin-top:70px">
      <div class="naviTitle">
        <span>点位名称、位置坐标</span>
      </div>
      <div style class="contentAdd">
        <div class="flexdis">
          <div style="width:100px;">经纬度</div>
          <div class="dateTime">
            <input
              type="text"
              v-model="lnglat"
              id="input"
              placeholder="请获取相关经纬度"
              @focus="getLngLat()"
            />
          </div>
        </div>
        <div class="flexdis">
          <div style="width:100px;">监测时间</div>
          <div class="dateTime">
            <!-- <mu-date-input
              v-model="value6"
              container="bottomSheet"
              label-float
              full-width
              type="dateTime"
              no-display
            ></mu-date-input> -->
            <input type="text" placeholder="请选择录入时间" class="input" disabled v-model="value6">
          </div>
        </div>
        <div class="flexdis">
          <div style="width:100px;">点位名称</div>
          <div class="dateTime" @click="selectDataPicoptions" id="classPicoptions">
            <!-- <input type="text" id="input" placeholder="请输入点位名称" v-model="pollutantName" /> -->
            <!-- <mu-select label v-model="pollutantName" full-width>
              <mu-option
                v-for="(option,index) in optionsenterprise"
                :key="index"
                :label="option"
                :value="option"
              ></mu-option>
            </mu-select> -->
            <input type="text" name="" class="input" placeholder="请选择点位名称" v-model="pollutantName" disabled>
          </div>
        </div>
      </div>
      <div class="naviTitle">
        <span>基本数据信息</span>
      </div>
      <div style="padding:0 5px">
        <div style="border-radius:10px">
          <div class="flexdis">
            <div style="width:80px;">pH</div>
            <div class="dateTime">
              <input type="text" id="inputData" placeholder v-model="dataList.ph" />
            </div>
            <div style="width:80px;">COD</div>
            <div class="dateTime">
              <input type="text" id="inputData" placeholder v-model="dataList.cod" />
              <span class="gray">mg/L</span>
            </div>
          </div>
          <div class="flexdis">
            <div style="width:80px;">总铜</div>
            <div class="dateTime">
              <input type="text" id="inputData" placeholder v-model="dataList.cuprum" />
              <span class="gray">mg/L</span>
            </div>
            <div style="width:80px;">氨氮</div>
            <div class="dateTime">
              <input type="text" id="inputData" placeholder v-model="dataList.ammoniaNitrogen" />
              <span class="gray">mg/L</span>
            </div>
          </div>
          <div class="flexdis">
            <div style="width:80px;">总镍</div>
            <div class="dateTime">
              <input type="text" id="inputData" placeholder v-model="dataList.nickel" />
              <span class="gray">mg/L</span>
            </div>
            <div style="width:80px;">总磷</div>
            <div class="dateTime">
              <input type="text" id="inputData" placeholder v-model="dataList.phosphorus" />
              <span class="gray">mg/L</span>
            </div>
          </div>
          <div class="flexdis">
            <div style="width:80px;">总铬</div>
            <div class="dateTime">
              <input type="text" id="inputData" placeholder v-model="dataList.chromium" />
              <span class="gray">mg/L</span>
            </div>
            <div style="width:80px;">总氮</div>
            <div class="dateTime">
              <input type="text" id="inputData" placeholder v-model="dataList.nitrogen" />
              <span class="gray">mg/L</span>
            </div>
          </div>
          <div class="flexdis">
            <div style="width:120px;">总氰化物</div>
            <div class="dateTime">
              <input type="text" id="inputData" placeholder v-model="dataList.prussiate" />
              <span class="gray">mg/L</span>
            </div>
          </div>
        </div>
        <div class="naviTitle">
          <span>现场照片</span>
        </div>
        <div class="HeaderTitleContent">
          <el-upload
            ref="upload"
            action="http://47.112.16.168:8803/explore"
            multiple
            name="files"
            list-type="picture-card"
            :on-change="handleRemove"
            :http-request="uploadFile"
            :auto-upload="false"
            :limit="4"
            :on-exceed="limitStatus"
          >
            <i class="el-icon-plus"></i>
          </el-upload>
          <el-dialog :visible.sync="dialogVisible" size="tiny">
            <img width="100%" :src="dialogImageUrl" alt />
          </el-dialog>
        </div>
        <div class="bottomBtn">
          <div class="submitImage" @click="submit()">
            <span>提交</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import AppBace from "../../../static/ajax";
import Vue from 'vue';
import { Toast } from 'vant';

Vue.use(Toast);
export default {
  name: "addData",
  data() {
    return {
      optionsenterprise: [],
      lnglat: "1",
      value6: "",
      dialogImageUrl: "",
      dialogVisible: false,
      token: "",
      formDate: {},
      dataList: {
        prussiate: 0,
        phosphorus: 0,
        ph: 0,
        nitrogen: 0,
        ammoniaNitrogen: 0,
        chromium: 0,
        nickel: 0,
        cod: 0,
        cuprum: 0
      },
      latitude: "",
      longitude: "",
      pollutantName: "",
      routingName: "",
      loading: false
      // pollutantName:""
    };
  },
  methods: {
     selectDataPicoptions() {
      // this.opened = false;
      var _this = this;
      var pickerView = new PickerView({
        bindElem: classPicoptions, // 绑定的元素,用于区别多个组件存在时回显区别，如果单个可以随意填某个元素
        data: this.$store.state.enterprise, // 说明：该参数必须符合json格式 且最里层是个数组，如上面的data变量所展示的[3,4]。
        title: "选择类型", // 顶部标题文本 默认为“标题”
        leftText: "取消", // 头部左侧按钮文本 默认为‘取消’
        rightText: "确定", // 头部右侧按钮文本 默认为“确定”
        rightFn: function(selectArr) {
          // 点击头部右侧按钮的回调函数，参数为一个数组，数组对应滚轮中每项对应的值
          console.log(selectArr[0]);
          _this.pollutantName = selectArr[0];
          // _this.opened = true;
        }
      });
    },
    fullscreen() {
      this.loading = this.$loading();
    },
    backTo() {
      this.$router.push({
        path: "addListDetails",
        query: {
          id: sessionStorage.getItem("deviceId")
        }
      });
    },
    getLngLat() {
      this.$router.push({ path: "/selectMap" });
    },
    handleRemove(file, fileList) {},
    uploadFile(res) {
      // this.formDate = new FormData();
      this.formDate.append("files", res.file);
    },
    limitStatus(e) {
      //  console.log(e)
      Toast("只能上传4张图片");
    },
    async submit() {
      this.fullscreen();
      // this.openAlert = false;
      var lnglat = sessionStorage.getItem("lnglat");
      if (lnglat) {
        this.longitude = lnglat.split(",")[0];
        this.latitude = lnglat.split(",")[1];
      }
      this.routingName = sessionStorage.getItem("routingName");
      this.formDate = new FormData();
      this.$refs.upload.submit();
      // 时间
      this.formDate.append("lastMonitor", AppBace.filterTime(this.value6));
      // 总氰化合物
      this.formDate.append("prussiate", this.dataList.prussiate);
      // 总磷
      this.formDate.append("phosphorus", this.dataList.phosphorus);
      // ph
      this.formDate.append("ph", this.dataList.ph);
      // 总氮
      this.formDate.append("nitrogen", this.dataList.nitrogen);
      //  氨氮
      this.formDate.append("ammoniaNitrogen", this.dataList.ammoniaNitrogen);
      // 总铬
      this.formDate.append("chromium", this.dataList.chromium);
      // 总镍
      this.formDate.append("nickel", this.dataList.nickel);
      // 化学含氧量
      this.formDate.append("cod", this.dataList.cod);
      // 总铜
      this.formDate.append("cuprum", this.dataList.cuprum);
      // 纬度
      this.formDate.append("latitude", this.latitude);
      // 经度
      this.formDate.append("longitude", this.longitude);
      // routingName
      this.formDate.append("routingName", this.routingName);
      // debugger;
      // 点位名称
      this.formDate.append("pollutantName", this.pollutantName);
      var url = "/mobile/routing/point";
      var res = await AppBace.request(url, "POST", this.formDate, this.token);
      console.log(res);
       this.loading.close();
      if (res.status === 200) {
        // this.loading.close();
        Toast(res.data.info);
        this.$router.push({
          path: "/addListDetails",
          query: {
            id: sessionStorage.getItem("deviceId")
          }
        });
      } else {
        this.loading.close();
        Toast("上传数据失败，请确认上传数据是否完整");
      }
    }
  },
  mounted() {
    this.lnglat = this.$route.query.id;
    this.optionsenterprise = this.$store.state.enterprise;
    this.token = sessionStorage.getItem("token");
    // $("#input").val(this.lnglat);
    // this.lnglat = sessionStorage.getItem("lnglat");
  }
};
</script>
<style scoped>
@import "../../../static/css/model.css";
#inputData {
  width: 70%;
  border: #ccc 1px solid;
  border-radius: 10px;
  padding: 0 5px;
  outline: none;
}
/* 标题栏 */
.naviTitle {
  margin-top: 10px;
  font-size: 14px;
  font-weight: 600;
  /* margin-left: 5px; */
  margin-left: 5px;
  border-left: #4ed995 5px solid;
  margin-bottom: 10px;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  padding-left: 5px;
}
.contentAdd {
  width: 100%;
  border-radius: 10px;
  padding: 0 5px;
}
#input {
  border-radius: 10px;
  width: 100%;
  outline: none;
  padding: 0 10px;
  height: 30px;
  border: 1px solid #ccc;
}
.flexdis {
  padding: 0 10px;
  display: flex;
  flex-direction: row;
  height: 50px;
  align-items: center;
  background: white;
  /* border-radius: 10px; */
}
.dateTime {
  width: 100%;
}
.gray {
  color: #ccc;
  margin-left: 5px;
}
.fiexdHeader {
  position: fixed;
  top: 0;
}
.HeaderTitleContent {
  padding: 0 5px;
}
.submitImage {
  background: #4ed995;
  width: 100px;
  height: 30px;
  /* align-items: center; */
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  color: white;
  box-shadow: 1px 5px 1px rgb(193, 252, 216);

  /* text-align: center; */
}
.bottomBtn {
  margin-bottom: 10px;
  width: 100%;
  height: 40px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header{
  position: fixed;
  top: 0;
}
</style>



